import { createStore } from 'vuex'
import axios from 'axios'
import router from '@/router'
// import DCM from './DCM'

export default createStore({
  state: {  

    currentSectionNumber: 0,
    actualSection: {},
    sectionList: [
      {
        serverTestId: 2,
        name:"Тестирование",
        nameForEndTest: "Начальное тестирование",
        description: "Ответить на 30 вопросов-тестов",
        routeStart: "/test0/start",
        status: "completed",
        rightAnswers: 0,
        wrongAnswers: 0
      },
      {
        serverTestId: 4,
        name:"Обучение",
        nameForEndTest: "Обучающие интерактивные сценарии",
        description: "Решить ситуационные задачи",
        routeStart: "/starttraining",
        status: "ready",
        rightAnswers: 0,
        wrongAnswers: 0
      },
      {
        serverTestId: 3,
        name:"Завершающее тестирование",
        nameForEndTest: "Завершающее тестирование",
        description: "Ответить на 30 вопросов-тестов",
        routeStart: "/test2/start",
        status: "notActive",
        rightAnswers: 0,
        wrongAnswers: 0
      }
    ],

    questionsList: [],
    totalQuestions: 2,
    actualView: {},
    feedbackQuestion: {},
    feedbackAnswer:{
      complexity: 0,
      comment: "",
      interestScenario: ""
    },
    rightAnswers: 0,
    wrongAnswers: 0,
    stepNumber: 0,
    currentAnswerIndex: "",
    sessionId:"",

    login: "",
    userName: "",
    token: "",
    isAuth: false,
    isLoginWarning: false
  },
  getters: {
    questionsList(state){
      return state.questionsList
    },
    actualView(state){
      return state.actualView
    },
    stepNumber(state){
      return state.stepNumber
    },
    totalQuestions(state){
      return state.totalQuestions
    },
    testsSummary(state){
      return state.testsSummary
    },
    isAuth(state){
      return state.isAuth
    },
    isLoginWarning(state){
      return state.isLoginWarning
    },
    userName(state){
      return state.userName
    },
    currentAnswerIndex(state){
      return state.currentAnswerIndex
    },
    sectionList(state){
      return state.sectionList
    },
    currentSectionNumber(state){
      return state.currentSectionNumber
    },
    actualSection(state){
      return state.actualSection
    },
    feedbackQuestion(state){
      return state.feedbackQuestion
    },
    feedbackAnswer(state){
      return state.feedbackAnswer
    }

  },
  mutations: {
    setNextStep(state){
      state.stepNumber += 1
    },
    setActualView(state){
      state.actualView = state.questionsList[state.stepNumber]
    },
    createQuestionsList(state, questionsData){

      const feedback = questionsData.find(e => e.question_type_id===3)
      state.feedbackQuestion = feedback
      
      const onlyQuestionsList = questionsData.filter(x => x.question_type_id === 2)

      //временная
      state.questionsList = onlyQuestionsList.slice(0, state.totalQuestions)
      
      //рабочая штука
      // state.questionsList = onlyQuestionsList.sort(() => Math.random() - Math.random()).slice(0, state.totalQuestions)

      // state.questionsList = questionsData.sort(() => Math.random() - Math.random()).slice(0, state.totalQuestions)

    },
    createSessionId(state, sessionId){
      state.sessionId = sessionId
    },
    showLoginModal(state){
      state.loginModal = true
    },
    hideLoginModal(state){
      state.loginModal = false
    },
    setUserData(state, data){
      state.login = data.login,
      state.userName = data.name,
      state.token = data.token.auth_token,
      state.isAuth = true
    },
    deleteUserData(state){
      state.login = "",
      state.userName = "",
      state.token = "",
      state.isAuth = false
    },
    showWarning(state){
      state.isLoginWarning = true
    },
    hideWarning(state){
      state.isLoginWarning = false
    },
    setCurrentAnswerIndex(state, answerIndex){
      state.currentAnswerIndex = answerIndex
    },
    setCurrentSectionNumber(state, sectionNumber){
      state.currentSectionNumber = sectionNumber
    },
    setActualSection(state){
      state.actualSection = state.sectionList[state.currentSectionNumber]
    },
    setFeedbackItem(state, feedbackItem){
      state.feedbackAnswer[feedbackItem.name] = feedbackItem.value
    },
    clearfeedbackAnswer(state){
      state.feedbackAnswer.complexity = 0,
      state.feedbackAnswer.comment = "",
      state.feedbackAnswer.interestScenario = ""
    },
    clearStateAnswers(state, testId){

      if (testId===100){
        console.log(testId)
      }
      
      state.rightAnswers = 0
      state.wrongAnswers = 0
      state.questionsList = []
      state.stepNumber = 0
    },
    increaseRightWrongAnswers(state){
      if (state.questionsList[state.stepNumber].body.answers.items[state.currentAnswerIndex]) {
        state.rightAnswers += 1
      }
      else{
        state.wrongAnswers += 1
      }
      
    },

    addSummaryToState(state, testSummary){
      
      for (let i = 0; i < state.sectionList.length; i++) {
        let summaryById = testSummary.find( result => result.test_id === state.sectionList[i].serverTestId)
        if (summaryById) {
          state.sectionList[i].rightAnswers = summaryById.right_answers
          state.sectionList[i].wrongAnswers = summaryById.wrong_answers
        }

        if (state.sectionList[i].rightAnswers>0||state.sectionList[i].wrongAnswers>0) {
          state.sectionList[i].status = "completed"
        }
        else{
          if (i > 0) {
            if (state.sectionList[i-1].status === "completed") {
              state.sectionList[i].status = "ready"
            }
            else{
              state.sectionList[i].status = "notActive"
            }
          }
          else{
            state.sectionList[i].status = "ready"
          }
        }

      }
    }

  },
//-----------------------------------------------
  actions: {
    SIGN_IN({commit}, loginPasswowrd){
      axios.post('https://dev1.med-game.ru/signin', 
        loginPasswowrd
      )
      .then(function (response) {
        console.log(response);
        commit('setUserData', response.data.data)
      })
      .then(function () {
        router.push('/main')
      })
      .catch(function (error) {
        commit('showWarning')
        console.log(error);
      });
    },

    SIGN_OUT({commit}){
      commit('deleteUserData')
    },

    GET_TEST_SUMMARY({commit, state}, ){
      axios({
        method: 'get',
        url: `https://dev1.med-game.ru/api/v1/user/results`,
        headers: { Authorization: `Bearer ${state.token}` }
      })
      .then((testSummary) => {
        commit('addSummaryToState', testSummary.data.data)        
      })
        
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },
    
    GET_TEST({commit, state}, testNumber){
      axios({
        method: 'get',
        url: `https://dev1.med-game.ru/api/v1/user/test/${testNumber}`,
        headers: { Authorization: `Bearer ${state.token}` }
      })
      .then((testData) => {
        commit('createSessionId', testData.data.data.session_id)
        commit('createQuestionsList', testData.data.data.questions)
        commit('setActualView')
        console.log(commit);
      })        
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },

    SEND_ANSWER({dispatch, commit, state}, dataAnswer){
      axios({
        url: 'https://dev1.med-game.ru/api/v1/user/answers',
        method: 'post',
        headers: {
          'session-id':  state.sessionId,
          Authorization: `Bearer ${state.token}` 
        },
        data: dataAnswer
      })
      .then(() => {
        if (state.stepNumber === state.questionsList.length-1) {
          commit('increaseRightWrongAnswers')
          commit('setCurrentAnswerIndex', "")          
          return dispatch('SEND_SUMMARY', dataAnswer.test_id)          
        }
        else{
          commit('increaseRightWrongAnswers')
          commit('setCurrentAnswerIndex', "")          
          commit('setNextStep')
          commit('setActualView')
        }
        
      })
      .catch(function (error) {
        console.log(error);        
      })
    },
    
    SEND_SUMMARY({commit, state}, testId){
      axios({
        url: `https://dev1.med-game.ru/api/v1/user/test/${testId}/summary`,
        method: 'post',
        headers: {
          'session-id':  state.sessionId,
          Authorization: `Bearer ${state.token}` 
        },
        data: {
          "right_answers": state.rightAnswers,
          "wrong_answers": state.wrongAnswers
        }
      })
      .then(() => {
        commit('clearStateAnswers')
        // router.push(`/test${state.currentSectionNumber}/endtest`)
        router.push(`/test${state.currentSectionNumber}/feedback`)
        
      })
      .catch(function (error) {
        console.log(error);        
      })
    },

    SEND_FEEDBACK({commit,state}, dataFeedbackAnswer){
      axios({
        url: 'https://dev1.med-game.ru/api/v1/user/answers',
        method: 'post',
        headers: {
          'session-id':  state.sessionId,
          Authorization: `Bearer ${state.token}` 
        },
        data: dataFeedbackAnswer
      })
      .then(() => {
        commit('clearfeedbackAnswer')
        router.push(`/test${state.currentSectionNumber}/endtest`)
      })
      .catch(function (error) {
        console.log(error);        
      })
    },

    GET_DATA_FROM_LOCAL({commit}, questionsData){
      commit('createQuestionsList', questionsData)
      commit('setActualView')
    },
    setNextStep({commit}){
      commit('setNextStep')
      commit('setActualView')
    },
    setQuestionsPageType({commit}, questionsPageType){
      commit('setQuestionsPageType', questionsPageType)
    },
    hideWarning({commit}){
      commit('hideWarning')
    },
    setCurrentAnswerIndex({commit}, answerIndex){
      commit('setCurrentAnswerIndex', answerIndex)
    },
    setCurrentSectionNumber({commit}, sectionNumber){
      commit('setCurrentSectionNumber', sectionNumber)
      commit('setActualSection')
    },
    setActualSection({commit}){
      commit('setActualSection')
    },
    setFeedbackItem(store, feedbackItem){
      store.commit('setFeedbackItem', feedbackItem)
    },
    
  },
  modules: {
    // DCM
  }
})
